
import { defineComponent } from "vue";
export default defineComponent({
  name: "Menu",
  data() {
    return { showAdult: false as boolean, showKids: false as boolean };
  },
  methods: {
    closeMenu() {
      this.$emit("closeMenu");
    },
    toggleShowAdult() {
      this.showAdult = true;
    },
    toggleShowKids() {
      this.showKids = true;
    },
    closeShowAdult() {
      this.showAdult = false;
    },
    closeShowKids() {
      this.showKids = false;
    },
  },
});
